export default {
	props: {
		// 
		showToolbar: {
			type: Boolean,
			default: true
		},
		// 
		title: {
			type: String,
			default: ''
		},
		// 
		round: {
			type: [String, Number],
			default: 0
		},
		// 
		columns: {
			type: Array,
			default: () => []
		},
		// 
		loading: {
			type: Boolean,
			default: false
		},
		// 
		itemHeight: {
			type: [String, Number],
			default: 44
		},
		// 
		cancelText: {
			type: String,
			default: ''
		},
		// 
		confirmText: {
			type: String,
			default: ''
		},
		// 
		cancelColor: {
			type: String,
			default: '#909193'
		},
		// 
		confirmColor: {
			type: String,
			default: '#3c9cff'
		},
		// 
		color: {
			type: String,
			default: ''
		},
		// 
		activeColor: {
			type: String,
			default: ''
		},
		// 
		visibleItemCount: {
			type: [String, Number],
			default: 5
		},
		// 
		keyName: {
			type: String,
			default: 'text'
		},
		// 
		closeOnClickOverlay: {
			type: Boolean,
			default: true
		},
		// 
		closeOnClickConfirm: {
			type: Boolean,
			default: true
		},
		// 
		defaultIndex: {
			type: Array,
			default: () => [],
		},
		//  change  change 2.21.1
		immediateChange: {
			type: Boolean,
			default: true
		},
		...uni.$uv?.props?.picker
	}
}