export default {
	props: {
		// 
		show: {
			type: Boolean,
			default: false
		},
		// z-index
		zIndex: {
			type: [String, Number],
			default: 10070
		},
		// ms
		duration: {
			type: [String, Number],
			default: 300
		},
		// rgba
		opacity: {
			type: [String, Number],
			default: 0.5
		},
		...uni.$uv?.props?.overlay
	}
}