export default {
	props: {
		// 
		name: {
			type: String,
			default: ''
		},
		// 
		color: {
			type: String,
			default: '#606266'
		},
		// px
		size: {
			type: [String, Number],
			default: '16px'
		},
		// 
		bold: {
			type: Boolean,
			default: false
		},
		// index
		index: {
			type: [String, Number],
			default: null
		},
		// 
		hoverClass: {
			type: String,
			default: ''
		},
		// 
		customPrefix: {
			type: String,
			default: 'uvicon'
		},
		// 
		label: {
			type: [String, Number],
			default: ''
		},
		// label
		labelPos: {
			type: String,
			default: 'right'
		},
		// label
		labelSize: {
			type: [String, Number],
			default: '15px'
		},
		// label
		labelColor: {
			type: String,
			default: '#606266'
		},
		// label
		space: {
			type: [String, Number],
			default: '3px'
		},
		// mode
		imgMode: {
			type: String,
			default: 'aspectFit'
		},
		// 
		width: {
			type: [String, Number],
			default: ''
		},
		// 
		height: {
			type: [String, Number],
			default: ''
		},
		// 
		top: {
			type: [String, Number],
			default: 0
		},
		// 
		stop: {
			type: Boolean,
			default: false
		},
		...uni.$uv?.props?.icon
	}
}