export default {
	props: {
		// 
		show: {
			type: Boolean,
			default: true
		},
		// 
		color: {
			type: String,
			default: '#909193'
		},
		// 
		textColor: {
			type: String,
			default: '#909193'
		},
		// 
		vertical: {
			type: Boolean,
			default: false
		},
		// circle-spinner-semicircle-
		mode: {
			type: String,
			default: 'spinner'
		},
		// px
		size: {
			type: [String, Number],
			default: 24
		},
		// 
		textSize: {
			type: [String, Number],
			default: 15
		},
		// 
		textStyle: {
			type: Object,
			default () {
				return {}
			}
		},
		// 
		text: {
			type: [String, Number],
			default: ''
		},
		//  https://www.runoob.com/cssref/css3-pr-animation-timing-function.html
		timingFunction: {
			type: String,
			default: 'linear'
		},
		// 
		duration: {
			type: [String, Number],
			default: 1200
		},
		// mode=circle
		inactiveColor: {
			type: String,
			default: ''
		},
		...uni.$uv?.props?.loadingIcon
	}
}