export default {
	props: {
		value: {
			type: [String, Number],
			default: 0
		},
		modelValue: {
			type: [String, Number],
			default: 0
		},
		// 
		count: {
			type: [String, Number],
			default: 5
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		readonly: {
			type: Boolean,
			default: false
		},
		// px
		size: {
			type: [String, Number],
			default: 18
		},
		// 
		inactiveColor: {
			type: String,
			default: '#b2b2b2'
		},
		// 
		activeColor: {
			type: String,
			default: '#FA3534'
		},
		// px
		gutter: {
			type: [String, Number],
			default: 4
		},
		// 
		minCount: {
			type: [String, Number],
			default: 1
		},
		// 
		allowHalf: {
			type: Boolean,
			default: false
		},
		// ()
		activeIcon: {
			type: String,
			default: 'star-fill'
		},
		// ()
		inactiveIcon: {
			type: String,
			default: 'star'
		},
		// 
		touchable: {
			type: Boolean,
			default: false
		},
		...uni.$uv?.props?.rate
	}
}