export default {
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		modelValue: {
			type: [String, Number],
			default: ''
		},
		// 
		show: {
			type: Boolean,
			default: false
		},
		// 
		showToolbar: {
			type: Boolean,
			default: true
		},
		// 
		title: {
			type: String,
			default: ''
		},
		// mode=datemode=timemode=year-monthmode=datetime
		mode: {
			type: String,
			default: 'datetime'
		},
		// 
		maxDate: {
			type: Number,
			// 10
			default: new Date(new Date().getFullYear() + 10, 0, 1).getTime()
		},
		// 
		minDate: {
			type: Number,
			// 10
			default: new Date(new Date().getFullYear() - 10, 0, 1).getTime()
		},
		// mode=time
		minHour: {
			type: Number,
			default: 0
		},
		// mode=time
		maxHour: {
			type: Number,
			default: 23
		},
		// mode=time
		minMinute: {
			type: Number,
			default: 0
		},
		// mode=time
		maxMinute: {
			type: Number,
			default: 59
		},
		// 
		filter: {
			type: [Function, null],
			default: null
		},
		// 
		formatter: {
			type: [Function, null],
			default: null
		},
		// 
		loading: {
			type: Boolean,
			default: false
		},
		// 
		itemHeight: {
			type: [String, Number],
			default: 44
		},
		// 
		cancelText: {
			type: String,
			default: ''
		},
		// 
		confirmText: {
			type: String,
			default: ''
		},
		// 
		cancelColor: {
			type: String,
			default: '#909193'
		},
		// 
		confirmColor: {
			type: String,
			default: '#3c9cff'
		},
		// 
		visibleItemCount: {
			type: [String, Number],
			default: 5
		},
		// 
		closeOnClickOverlay: {
			type: Boolean,
			default: true
		},
		// 
		closeOnClickConfirm: {
			type: Boolean,
			default: true
		},
		// 
		clearDate: {
			type: Boolean,
			default: false
		},
		// 
		round: {
			type: [String, Number],
			default: 0
		},
		...uni.$uv?.props?.datetimePicker
	}
}